.featured{
    margin-top: 5rem;
}
.featured .featured-text{
    text-align: center;
    margin: 1rem 0;
}
.featured .container{
    --gap: 1.2rem;
    --num-cols: 5;
    --row-height: 300px;

    padding: 1rem;
    display: grid;
    grid-template-columns: repeat(var(--num-cols), 1fr);
    grid-auto-rows: var(--row-height);
    gap: var(--gap);
}
.featured .container img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.span-3{
    grid-column: span 3;
}
.span-2{
    grid-column: span 2;
}
.image-grid-row-2{
    grid-row: span 2;
}
.info-grid{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.right-img-details .btn{
    margin: 1rem 0;
}
.featured .top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.featured .top .price{
    font-weight: 600;
    color: #00d000;
    font-size: 1.5rem;
}
.featured .info{
    display: flex;
    justify-content: space-between;
    margin: .8rem 0;
    width: 70%;
}
.featured .bold{
    font-weight: 600;
    margin-right: 1rem;
}
@media screen and (max-width: 1024px) {
    .featured .container {
        --num-cols: 2;
        --row-height: 200px;
        --grap: .5rem;
    }
    .span-3{
        grid-column: span 2;
    }
    .order-1{
        order: 1;
    }
    .order-2{
        order: 2;
    }
    .order-3{
        order: 3;
    }
    .order-4{
        order: 4;
    }
    .order-5{
        order: 5;
    }
    .order-6{
        order: 6;
    }
    .order-7{
        order: 7;
    }
}



